.wrapper {
	padding: 100px;
}

.image--cover {
	width: 150px;
	height: 150px;
	border-radius: 50%;
	margin: -67px;
	-o-object-fit: cover;
	   object-fit: cover;
	-o-object-position: center right;
	   object-position: center right;
	border: 2px #fff solid;
	opacity: 0;
}

#name {
	opacity: 0;
	margin-top: -10px;
}

#accountNo {
	opacity: 0;
	margin-top: -12px;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

li.menu-list {
	padding-right: 25px;
	font-size: 16px;
	opacity: 0;
}

.menu-list-item {
	text-align: right;
}
.menu-list-item h6 {
	margin-top: 10px;
	color: #7a78a2 !important;
}
.menu-list-item h1 {
	margin-top: -28px;
}

.logOut {
	border: none;
	background-color: #21212d;
	color: wheat;
	border-radius: 21px;
	padding: 5px;
	width: 236px;
	-webkit-transition: all 0.2s ease-in;
	-o-transition: all 0.2s ease-in;
	transition: all 0.2s ease-in;
	outline: none;
	opacity: 0;
	position: absolute;
	bottom: 0;
	margin: 10px 0 25px -118px;
}
.logOut:hover {
	background-color: #fff;
	color: black;
}
.logOut:active {
	-webkit-transform: translateY(2px);
	    -ms-transform: translateY(2px);
	        transform: translateY(2px);
}

.sideArrow {
	position: absolute;
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	        transform: rotate(180deg);
	right: 23px;
	font-size: 26px;
	display: none;
}

@media screen and (max-width: 768px) {
	.sideArrow {
		display: block;
	}
}
.menu {
	display: block;
}

@media screen and (max-width: 768px) {
	.menu {
		display: none;
	}
}

.logo-main {
	width: 71px;
	margin-top: -15px;
	margin-bottom: -42px;
	margin-left: -162px;
}



