.yet-to-buy {
	background-color: red !important;
}
.bought {
	background-color: green !important;
}

.product-list {
	display: -ms-flexbox;
	display: flex;
}

.product-list .products-list {
	width: 100%;
}

.products-list ul {
	width: 100%;
	overflow-y: scroll;
	padding: 0 10px;
	background: white;
	margin-top: -25px;
	height: 64vh;
}

.products-list h2 {
	text-align: center;
}

@media screen and (max-width: 425px) {
	.tab li {
		width: 100%;
	}

	.product-list {
		display: -ms-flexbox;
		display: block;
	}
	.products-list ul {
		height: 320px !important;
	}
}

.tabs_item.product-request {
	padding: 30px;
}

.product-user-list li {
	padding: 11px;
	margin: 10px 0px;
	background: #f4f3f3;
}

.tabs_item .info .name .product-user {
	color: #ffff;
	background: #3268fb;
	width: 156px;
	height: 20px;
	line-height: 2;
	margin-top: 5px;
	float: left;
	border-radius: 2px;
	text-align: center;
	top: -24px;
	right: 0;
	font-size: 10px;
	font-weight: 600;
}

.btn-pool.btn-effect:disabled {
	opacity: 0.5;
	cursor: not-allowed;
}

.buying-list {
	display: -ms-flexbox;
	display: flex;
}

.buying-list button.btn-1 {
	margin: 0 2px 0 0;
}

.buying-list button.btn-2 {
	margin: 0 0 0 2px;
}

.buying-list button.remove-from-list {
	background-color: red;
}

@media screen and (max-width: 426px) {
	.buying-list {
		display: block !important;
	}
	.buying-list button.btn-1 {
		margin: 0 2px 4px 0;
	}
}

.pool-list ul > li.transaction-list {
	padding: 10px;
	background-color: #f3f3f3;
	margin: 20px 0px;
	text-align: left;
	font-family: monospace !important;
	font-weight: bold;
	color: #2d2d2d;
}

.info .transaction-details {
	overflow-y: scroll;
	width: 100%;
	text-align: center;
}

.info .transaction-details::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgb(244, 243, 243) !important;
	border-radius: 1px;
	display: none;
}

.info .transaction-details::-webkit-scrollbar {
	width: 0.1px;
	height: 1px;
	background-color: transparent;
}

.info .transaction-details::-webkit-scrollbar-thumb {
	border-radius: 1px;
	-webkit-box-shadow: inset 0 0 6px rgb(244, 243, 243) !important;
	background-color: rgb(244, 243, 243);
}



