#bottombar-menu {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	height: 100%;
}
#bottombar-menu .bottom-menu-list {
	padding: 0 10px;
	margin: 0;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}
#bottombar-menu li {
	padding: 0;
	margin: 0 auto;
}
#bottombar-menu li:first-child {
	margin: 0;
	padding: 0;
	-ms-flex-pack: center !important;
	    justify-content: center !important;
}
#bottombar-menu li.bottom-menu-list h6 {
	text-align: center;
	color: #7a78a2;
}
#bottombar-menu li.bottom-menu-list h1 {
	text-align: center;
	margin: -24px 0 -7px;
	color: white;
}

@media (min-width: 626px) and (max-width: 888px) {
	#bottombar-menu {
		width: 772px;
	}
}

@media (min-width: 320px) and (max-width: 626px) {
	#bottombar-menu {
		width: 576px;
	}
}
@media screen and (max-width: 320px) {
	#bottombar-menu {
		margin-top: -6px;
	}
	#bottombar-menu li.bottom-menu-list h6 {
		text-align: center;
		font-size: 9px;
	}
	#bottombar-menu li.bottom-menu-list h1 {
		text-align: center;
		font-size: 20px;
		margin-top: -18px;
	}
}
@media screen and (max-width: 768px) {
	#bottombar-menu {
		margin-top: 1px;
	}
	#bottombar-menu li.bottom-menu-list h6 {
		margin-top: 12px;
	}
	#bottombar-menu li.bottom-menu-list h1 {
		text-align: center;
		font-size: 25px;
		margin-top: -18px;
	}
}
.main-menu {
	position: absolute;
	display: block;
	width: 98%;
	height: 74px;
	overflow-y: hidden;
	overflow-x: scroll;
	white-space: nowrap;
	-webkit-overflow-scrolling: touch;
}
.main-menu ul {
	position: relative;
	display: inline-block;
	width: auto;
	margin: 0px;
	padding: 0px;
	list-style: none;
	overflow: hidden;
	float: left;
}
.main-menu ul li {
	display: inline-block;
	margin: 0px;
	padding: 10px 20px;
}



